<script>

	export default {

		functional: true,

		name: "latte-moment",

		props: {

			format: {
				default: "d MMMM YYYY",
				required: false,
				type: String
			},

			ms: {
				default: () => Date.now(),
				required: false,
				type: Number
			}

		},

		render(h, {parent, props, _v})
		{
			return _v(parent.moment(props.ms).format(props.format));
		}

	}

</script>
